export function initAnimation() {
    setTimeout(function () {
        const svgRoot = document.getElementById('root');
        const circleGroup = document.getElementById('circle-group');
        const cog = document.getElementById('LahsABNbo_ZUxYB51WBsM');

        if (!circleGroup || !cog) {
            console.error("SVG elements not found!");
            return;
        }

        const initialCogPositionX = 68.5;
        const initialCogPositionY = 23;
        const circleRadius = 8;
        const circleDiameter = circleRadius * 2;
        const currentMinY = 167.56;
        const currentMaxY = -156.85;
        const adjustedMinY = currentMinY - circleDiameter * 8.5;
        const adjustedMaxY = currentMaxY + circleDiameter * 1.5 + 24 + 16 + 8;
        let rotation = 0;
        const scalingFactor = 0.5;

        function updateCirclePosition(circleLocation) {
            circleLocation = Math.max(0, Math.min(circleLocation, 100));
            const newY = adjustedMinY + ((adjustedMaxY - adjustedMinY) * (circleLocation / 100));
            circleGroup.setAttribute('transform', `translate(0, ${newY})`);
        }

        function rotateCenterPiece(rotationSpeed) {
            const bbox = cog.getBBox();
            const cx = bbox.x + bbox.width / 2;
            const cy = bbox.y + bbox.height / 2;
            if (rotationSpeed > 0) {
                rotation -= rotationSpeed / 2;
            }
            cog.setAttribute(
                'transform',
                `translate(${initialCogPositionX}, ${initialCogPositionY}) scale(${scalingFactor}) rotate(${rotation}, ${cx}, ${cy})`
            );
        }

        function fetchCircleLocation() {
            return 0; // Update this to fetch the actual circle position if needed
        }

        function fetchRotationSpeed() {
            return 100; // Update this to fetch the actual rotation speed if needed
        }

        function animate() {
            const circleLocation = fetchCircleLocation();
            const rotationSpeed = fetchRotationSpeed();
            updateCirclePosition(circleLocation);
            rotateCenterPiece(rotationSpeed);
            requestAnimationFrame(animate);
        }

        animate();
    }, 500);
}
